<script>
    import {currentSong} from "../globals.js";
    
    let selectedSong = null;
    currentSong.subscribe(value => {
       selectedSong = value; 
    });
</script>

<style>
    .song-info {
        background: #17191a;
        height: 100vh;
        width: 20vw;
        
        position: fixed;
        top: 0;
        right: 0;
    }

    .page-table {
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
        color: white;
    }

    table {
        border-collapse: collapse;
        width: 98.5%;
    }

    th {
        padding: 8px 50px 8px 50px;
        text-align: left;
    }

    td {
        padding: 8px 50px 8px 50px;
        text-align: left;
    }

    table, th, td {
        border: none;
    }
</style>

<div class="song-info">
    {#if selectedSong}
        <p style="color: white;  padding: 8px">{selectedSong.name}</p>
    {/if}
</div>